@import url(https://fonts.googleapis.com/css2?family=Archivo+Black&family=Black+Ops+One&family=Changa:wght@200&family=Luckiest+Guy&family=Orelega+One&family=PT+Sans&family=Paytone+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kanit:wght@100&display=swap);

:root {
  --box-shadow-blue: 0px 0px 5px #0077ff99, 0 0px 2px rgba(0, 31, 204, 0.8);
  --box-shadow-blue-selected: 
      1px 1px 5px #dbecff,
      -1px -1px 5px #91c4ff,
      0px 0px 5px #0077ff99,
      0px 0px 10px #0077ff99,
      0px 0px 15px #0077ff99,
      0px 0px 20px #0077ff99;

  --neon-border-color: #0077ff99;

  --box-shadow-white: 1px 1px 5px rgba(255, 255, 255, 0.4), 0 1px 2px rgba(255, 255, 255, 0.5);

  --background-dark: #000307;
  --forebround-blue: #74b5ff;
  --forebround-light-blue: #b7d9ff;
  --forebround-blue-faded: #006deb88;

  }

body{
  /* background-color: #242b36; */
  background-color: #141a24;
  font-family: 'Kanit', sans-serif;
  color: #bdc2d4;
  margin: 0;
  background-image: url("https://www.transparenttextures.com/patterns/3px-tile.png");
}

.container {
    margin: auto;
    width: 100%;
}

.prison-door {

  margin: 1em 0.5em 0em 0.5em;
  border-radius: 20px 20px 0px 0px;
      background-color: #000307;
      background-color: var(--background-dark);
    display: -webkit-flex;
    display: flex;
    /* justify-content: space-between; */
    height: 270px;
    width: 13em;

    background-image: url(data:image/png;base64,);

    /* Major performance reduction -->  */
    /* box-shadow: var(--box-shadow-blue); */

    --text-shadow-prison-number: none;
    --prison-number-color: var(--forebround-blue-faded);
    --prison-number-color-hover: var(--forebround-blue);
  }

  .prisoner-name{
    font-size: 24px;
  }

  .prison-door.is-selected  {
    --text-shadow-prison-number: 
      0px 0px 5px #006debff,
      0px 0px 10px #006debff,
      0px 0px 15px #006debff,
      0px 0px 25px #006debff,
      0px 0px 35px #006debff,
      0px 0px 50px #006debff,
      0px 0px 70px #006debff;
    --prison-number-color: rgb(218, 235, 255);
    --prison-number-color-hover: rgb(218, 235, 255);;

    box-shadow: 1px 1px 5px #dbecff,
      -1px -1px 5px #91c4ff,
      0px 0px 5px #0077ff99,
      0px 0px 10px #0077ff99,
      0px 0px 15px #0077ff99,
      0px 0px 20px #0077ff99;;

    box-shadow: var(--box-shadow-blue-selected);
  }

  .glow{
    text-shadow: var(--text-shadow-prison-number);
    color: var(--prison-number-color);
  }

  .glow:hover{
    color: var(--prison-number-color-hover);
    cursor: pointer;
  }

  .prison-door > div {
    -webkit-flex: 1 1;
            flex: 1 1;
    text-align: center;
  }

  .prison-door-inner-border{
    margin: 10px;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0px 0px 5px #0077ff99, 0 0px 2px rgba(0, 31, 204, 0.8);
    box-shadow: var(--box-shadow-blue);
    width: 100%;
  }
  
  .prison-grate{
    margin: 20px 10% 0 10%;
    height: 80px;
    border-radius: 10px;
    background-color: #343434;
  }

  .prison-grate > div{
    box-sizing: border-box;
    background-color: #aaaacc;
    height: 100%;
    width: 7%;
    margin: 0 10px;
    display: inline-block;
    border-right: 2px solid white;
    border-left: 2px solid gray;
  }

  .prisoner-number{
    font-family: 'Changa', sans-serif;
    font-size: 30px;
    margin: 10px 30px;
  }



  .engraved {
    background-color: black;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    background-clip: text;
    color: transparent;
    text-shadow: rgba(200,200,200,0.9) -1px 1px 1px;
 }
 
 .details-container{
   margin: auto;
  border-radius: 20px 20px 0px 0px;
  background-color: #000307;
  background-color: var(--background-dark);
  display: -webkit-flex;
  display: flex;
  /* justify-content: space-between; */
  width: 50%;
  color: #74b5ff;
  color: var(--forebround-blue);

 }

 .left-container{
  -webkit-flex: 0 1 50%;
          flex: 0 1 50%;
  min-height: 100%;
  max-width: 50%;
 }

 .right-container{
  -webkit-flex: 0 1 50%;
          flex: 0 1 50%;
  display: -webkit-flex;
  display: flex;
  height: 100%;
  max-width: 50%;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  box-sizing: border-box;

  padding: 2vw;

  border-left-width: 1px;
  border-right-width: 0px;
  border-top-width: 3px;
  border-style: solid;
  border-image: 
    linear-gradient(to bottom, rgba(0, 0, 0, 0), #74b5ff, rgba(0, 0, 0, 0)) 1 100%;
  border-image: 
    linear-gradient(to bottom, rgba(0, 0, 0, 0), var(--forebround-blue), rgba(0, 0, 0, 0)) 1 100%;

 }

 .flex{
   display: -webkit-flex;
   display: flex;
 }

 img{
  object-fit: contain;
  width: 50%;
  margin: auto;
  display: -webkit-flex;
  display: flex;
 }

 .photo{
  background-image: url(/prisonmanager/static/media/wireframe_blue.e8022232.png);
  background-position: center;
  background-size: cover;
  /* position: relative; */
  width: 100%;
  /* height: 20vw; */
  height: 35vw;



  -webkit-animation: opacity-pulse 2s alternate infinite;



          animation: opacity-pulse 2s alternate infinite;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
 }

 .photo-frame{

 }

 @-webkit-keyframes opacity-pulse{
   0%{
     opacity: 40%;
   }
   100%{
     opacity: 80%;
   }
 }

 @keyframes opacity-pulse{
   0%{
     opacity: 40%;
   }
   100%{
     opacity: 80%;
   }
 }


 .hr-1{
 
   --box-shadow-hr-start:
   0px 0px 2px #91c4ff,
   0px 0px 5px #0077ff99,
   0px 0px 10px #0077ff99,
   0px 0px 15px #0077ff99,
   0px 0px 20px #0077ff99,
   0px 0px 30px #0077ff99,
   0px 0px 50px #0077ff99;

   --box-shadow-hr-end:
   0px 0px 5px #0077ff99,
   0px 0px 10px #0077ff99,
   0px 0px 15px #0077ff99;

  box-shadow: var(--box-shadow-hr-start);
  border-color: #b7d9ff;
  border-color: var(--forebround-light-blue);
  -webkit-animation: pulse 2s infinite;
          animation: pulse 2s infinite;
  /* animation-timing-function: cubic-bezier(0.82, 0.14, 0.74, 0.38); */
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
 }

 @-webkit-keyframes pulse {
   100%{
     box-shadow: none;
    /* border-color: var(--forebround-blue-faded); */
    opacity: 20%;
   }
 }

 @keyframes pulse {
   100%{
     box-shadow: none;
    /* border-color: var(--forebround-blue-faded); */
    opacity: 20%;
   }
 }

 .hr-2{
  border: 0;
  height: 0.3em;
  /* width: 200%; */
  /* background: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1), rgba(255, 255, 255, 0)); */
  --turn: 0;
  /* --start: 0%;
  --end: calc(var(--start) + 4%);
  --end: 5%; */
  background: linear-gradient(to left, #b7d9ff00,#b7d9ff00, 48%, #74b5ff , 52%, #b7d9ff30, #b7d9ff20, #b7d9ff10, #b7d9ff00);
  background: linear-gradient(to left, #b7d9ff00,#b7d9ff00, 48%, var(--forebround-blue) , 52%, #b7d9ff30, #b7d9ff20, #b7d9ff10, #b7d9ff00);
  background-size: 200%;
  background-position: 0% 0%;
  box-shadow:  
  0px 0px 5px #0077ff99;

  
    
  -webkit-animation: moving-linear 1s infinite;

  
    
          animation: moving-linear 1s infinite;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
}

@-webkit-keyframes moving-linear{
  50%{
    box-shadow:   none;
  }
  100%{
    background-position: -200% 0%;
  }
}

@keyframes moving-linear{
  50%{
    box-shadow:   none;
  }
  100%{
    background-position: -200% 0%;
  }
}






 



